.resume {
    display: flex;
    flex-direction: column;

    .section-header {
        margin-top: 20px;
        justify-content: center;
    }
    
    .divider {
        border-top: 2px solid black;
        margin-top: 5px;
    }
    
    .header {
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-top: 20px;
        margin-bottom: 10px;
    
        .company-info {
            margin-left: 30px;
            font-size: 20px;
            display: flex;
            flex-direction: column;
            align-items:flex-start;
    
            .company-name {
                font-weight: 800;
            }
        }
    
        .company-logo {
            border: 2px solid black;
            border-radius: 20px;
            width: 60px;
        }
    }
    
    .description {
        padding-left: 20px;
        padding-right: 20px;
        font-size: 20px;
        line-height: 20pt;
        
        li {
            margin-bottom: 10px;
        }
    
        @keyframes highlight {
            0% {
                color: red;
            }
            100% {
                color: none;
            }
        }
        
        .highlight {
            animation: highlight 3s;
        }
    }    
}
